<template>
  <v-container fluid class="ma-0 pa-0">
    <v-app-bar v-if="selected.length" color="grey darken-4" dark app>
      <v-app-bar-nav-icon v-if="!selected.length"></v-app-bar-nav-icon>
      <v-btn v-else icon @click="(selected = []), $emit('hideBar', selected)">
        <v-icon>mdi-close</v-icon>
      </v-btn>

      <v-toolbar-title> {{ selected.length }} selected </v-toolbar-title>

      <v-spacer></v-spacer>

      <v-btn icon>
        <v-icon>mdi-dots-vertical</v-icon>
      </v-btn>
    </v-app-bar>
    <v-container>
      <v-text-field
        v-model="searchFirma"
        single-line
        prepend-inner-icon="mdi-magnify"
        hide-details
        filled
        class="text-h6"
        rounded
      >
      </v-text-field>
    </v-container>
    <v-card class="elevation-0">
      <v-list-item-group
        v-model="selected"
        active-class="pink--text"
        multiple
        @change="$emit('hideBar', selected)"
      >
        <template v-for="f of searchCompany" class="pa-0">
          <v-list-item :key="f.firmen_id">
            <v-list-item-content>
              <v-list-item-title v-text="f.firmen_name" class="text-h5 font-weight-bold"></v-list-item-title>
              <v-list-item-subtitle class="text-h6">{{ f.fachrichtung }}</v-list-item-subtitle>

              <v-list-item-subtitle class="text-h6">{{ f.platz }}</v-list-item-subtitle>
            </v-list-item-content>
            <v-list-item-action>
              <v-btn icon :to="`/firma/${f.firmen_id}`"><v-icon> mdi-forwardburger </v-icon></v-btn>
            </v-list-item-action>
          </v-list-item>
        </template>
      </v-list-item-group>
    </v-card>
  </v-container>
</template>

<script>
export default {
  props: {
    firmen: {
      type: Array,
    },
  },
  data() {
    return {
      selected: [],
      searchFirma: '',
    };
  },
  computed: {
    searchCompany() {
      return this.firmen.filter((el) => el.firmen_name.includes(this.searchFirma));
    },
  },
};
</script>

<style lang="scss" scoped></style>
